// Здесь используем комментарии только через "//", т.к. они нам не нужны в итоговом css-файле
$fz_base: 16;
$fz: $fz_base;
// ##########
// Базовые настройки сетки сайта
// ##########
$container-width: 1300; // Максимальная ширина контейнера контента
$container-padding: 10; // Отступы слева и справа у контейнера
$grid-container-width: 100%; // Ширина контейнера сетки
$grid-cols: 12; // Количество колонок в сетке
$grid-gap: 30; // Расстояние между колонками
$grid-gap-vertical: 30; // Расстояние между строками в сетке
$breakpoints: (
  'xl': 1920px,
  'lg': 1680px,
  'md': 1024px,
  'sm': 768px,
  'xs': 480px,
);

// ##########
// Цвета
// ##########
$color-basic: black; // Основной цвет текста (применяется к body)
$color-placeholder: #777; // Основной цвет для placeholder
$color-input: black; // Основной цвет input, select и textarea
$color-main: #2252d8; // Основной фирменный цвет
$color-border: #ccc; // Основной цвет границ таблиц, секций

// ##########
// Transitions
// ##########
$ease-main: 0.2s ease;

// ##########
// Шрифты
// ##########
$font-main: "Gilroy", sans-serif; // Основной шрифт сайта
$font-second: "Arial", sans-serif; // Дополнительный шрифт


