.button-tag {
  $self: &;

  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;

  border-radius: em(8, $fz);
  padding: em(8, $fz);
  width: em(40, $fz);
  height: em(40, $fz);
  background-color: var(--system-bg);

  &.is-product-card {
    background-color: rgba(#f5f5f5, 0.4);
  }

  &__count {
    $fz: 13;
    font-size: em($fz, $fz_base);
    font-weight: 500;

    position: absolute;
    top: em(-10, $fz);
    right: 0;
    transform: translate3d(50%, -50%, 0);

    display: flex;
    align-items: center;
    justify-content: center;

    width: em(16, $fz);
    height: em(16, $fz);


    background: var(--system-primary);
    border-radius: 8px;

    z-index: 2;

    @include screen(md) {
      color: var(--system-primary);

      top: em(10, $fz);
      right: em(10, $fz);
      width: em(8, $fz);
      height: em(8, $fz);
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;

    width: em(24, $fz);
    height: em(24, $fz);

    color: transparent;

    #{$self}--is-favorite & {
      color: var(--system-primary);
    }

    svg {
      @include fill-img;
    }
  }
}

.button {
  $self: &;

  display: flex;
  align-items: center;

  border-radius: em(8, $fz);
  padding: em(8 12, $fz);
  min-width: em(126, $fz);
  min-height: em(32, $fz);

  background-color: var(--system-white);

  &--primary {
    border-radius: em(16, $fz);
    padding: em(20 48, $fz);
    background-color: var(--system-primary);
    justify-content: center;

    #{$self}__label {
      $fz: 18;
      font-size: em($fz, $fz_base);
      font-weight: 600;
      letter-spacing: 0.01em;
      color: var(--text-color-black);
    }
  }

  &__label {
    $fz: 14;
    font-size: em($fz, $fz_base);
    font-weight: 500;
    letter-spacing: 0.01em;
    color: var(--text-pallet-900);
  }

  &__icon {
    width: em(16, $fz);
    height: em(16, $fz);

    margin-left: em(10, $fz);

    fill: transparent;
  }

  &[disabled] {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:hover {
    opacity: 0.5;
  }
}
