.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-color: rgba(#666, 0.4);
  z-index: 999;

  display: none;

  &.is-active {
    display: block;
  }
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);

  width: 100%;

  border-radius: em(24, $fz);
  background: var(--system-white);
  padding: em(32 24, $fz);
  z-index: 1000;

  &__close {
    position: absolute;
    top: em(12, $fz);
    right: em(12, $fz);
    border-radius: em(20, $fz);
    padding: em(8, $fz);
    width: em(40, $fz);
    height: em(40, $fz);

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;

    margin-left: auto;
    margin-right: auto;

    width: em(92, $fz);
    height: em(92, $fz);

    border-radius: 100%;
    background-color: var(--sistem---600);

    margin-bottom: em(16, $fz);
  }

  &__svg {
    width: em(64, $fz);
    height: em(64, $fz);
  }

  &__title {
    $fz: 24;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    text-align: center;
    color: var(--text-black);
    margin-bottom: em(24, $fz);
  }

  &__description {
    font-weight: 500;
    letter-spacing: 0.01em;
    color: var(--text-pallet-600);
  }

  display: none;

  &.is-active {
    display: block;
  }
}

.success-modal {
  max-width: em(300, $fz);
  text-align: center;
}

.steps-modal {
  $self: &;

  max-width: em(632, $fz);

  @include screen(md) {
    max-width: 95%;
  }

  &__icon {
    width: em(40, $fz);
    height: em(40, $fz);

    border-radius: 100%;
    background-color: var(--system-white);
    color: var(--system-stroke);
    border: 1px solid;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    margin-right: em(14, $fz);

    #{$self}__item--check & {
      background-color: #D3FFBE;
      color: #404143;
      border: none;
    }
  }

  &__svg {
    width: em(24, $fz);
    height: em(24, $fz);
  }

  &__label {
    $fz: 18;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-pallet-600);

    #{$self}__item--check & {
      color: var(--text-pallet-900);
    }
  }

  &__list {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: em(20, $fz);
      top: 50%;
      transform: translate3d(-50%, -50%, 0);
      height: 90%;
      border: 1px dashed #DEB600;
      z-index: -1;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    line-height: 1.4;

    &:not(:first-child) {
      margin-top: em(28, $fz);
    }
  }
}

.product-info-modal {
  max-width: em(588, $fz);

  @include screen(md) {
    max-width: 95%;
  }
}

.search-modal {
  $self: &;

  position: relative;
  z-index: 10000;

  &.is-popup {
    display: none;

    #{$self}__label,
    #{$self}__btn {
      background-color: var(--system-bg);
    }

    #{$self}__container {
      padding: em(40 24, $fz);
    }
  }

  &.is-popup.is-active {
    display: block;
  }

  &__msg {
    width: 100%;
    margin-top: em(10, $fz);
  }

  &__container {
    display: flex;
    flex-wrap: wrap;

    border-radius: em(0 0 16 16, $fz);
    padding: em(40 24 20, $fz);

    background-color: var(--system-white);
  }

  &__label {
    flex-grow: 1;
    flex-shrink: 0;
    background-color: var(--system-white);
    display: flex;
    align-items: center;

    border: 1px solid var(--system-stroke);
    border-radius: em(12, $fz);
    padding-left: em(12, $fz);
    padding-right: em(12, $fz);
  }

  &__input {
    width: 100%;
    height: 100%;

    &::placeholder {
      font-weight: 500;
      letter-spacing: 0.01em;
      color: var(--text-pallet-400);
    }
  }

  &__btn {
    margin-right: em(10, $fz);
    background-color: var(--system-white);
  }

  &__close {
    width: em(56, $fz);
    height: em(56, $fz);
    margin-left: em(20, $fz);
  }
}
