.tabs {
  &__links {
    display: flex;
    width: 100%;
    margin-bottom: 20px;

    @include screen(md) {
      justify-content: space-between;
    }
  }

  &__link {
    $fz: 18;
    font-size: em($fz, $fz_base);
    border-radius: em(8, $fz);
    padding: em(4 12, $fz);
    font-weight: 600;
    letter-spacing: 0.01em;
    text-align: center;
    color: var(--text-pallet-600);

    @include screen(md) {
      min-width: em(110, $fz);
    }

    &--active {
      background-color: var(--system-white);
      color: var(--text-black);
    }
  }

  &__content {
    .tabs__pane {
      display: none;

      &--active {
        display: block;
      }
    }
  }
}
