.demo-links {
  display: inline-flex;
  flex-direction: column;

  &__item {
    position: relative;
    font-weight: 600;
    text-transform: uppercase;
    display: inline;

    $fz: 24;
    font-size: em($fz, $fz_base);

    &::before {
      content: '';
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 0;
      height: 2px;
      background-color: var(--system-primary);

      opacity: 0;

      transition: opacity $ease-main, width $ease-main;
    }

    &.is-active,
    &:hover {
      &::before {
        opacity: 1;
        width: 100%;
      }
    }

    &:not(:first-child) {
      margin-top: em(20, $fz);
    }
  }
}
