.filters-checkbox {
  $self: &;

  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  font-weight: 500;
  letter-spacing: 0.01em;
  padding-left: em(36, $fz);

  cursor: pointer;

  &__input {
    display: none;

    &:checked + #{$self}__check {
      color: var(--text-primary-txt);

      #{$self}__icon {
        opacity: 1;
      }

      & + #{$self}__label {
        color: var(--text-black);
      }
    }
  }

  &__label {
    color: var(--system-neutral-300);
  }

  &__count {
    color: var(--text-pallet-600);
  }

  &__check {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%) translateZ(0);

    width: em(24, $fz);
    height: em(24, $fz);

    border: 1px solid;
    border-radius: em(6, $fz);
    color: var(--system-neutral-300);

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__icon {
    width: em(24, $fz);
    height: em(12, $fz);
    opacity: 0;
  }
}

.filters-accordion {
  $self: &;

  &__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__label {
    $fz: 20;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-pallet-600);

    #{$self}.is-active & {
      color: var(--text-black);
    }
  }

  &__icon {
    width: em(24, $fz);
    height: em(18, $fz);

    #{$self}.is-active & {
      transform: rotate(-90deg);
    }
  }

  &__item {
    &:not(:first-child) {
      margin-top: em(18, $fz);
    }
  }

  &__list {
    display: none;

    padding-top: em(16, $fz);

    #{$self}.is-active & {
      display: block;
    }
  }
}

.filters {
  $self: &;

  &__toggle {
    border-radius: em(12, $fz);
    padding: em(16, $fz);
    width: em(56, $fz);
    height: em(56, $fz);
    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.12);
    background-color: var(--icon-hover);

    display: flex;
    align-items: center;
    justify-content: center;

    color: #fff;
    cursor: pointer;

    #{$self}.is-active & {
      background-color: var(--system-white);
      color: var(--text-primary-txt);
    }

    &::before {
      content: '';
      position: absolute;
      top: em(4, $fz);
      right: em(4, $fz);
      width: em(12, $fz);
      height: em(12, $fz);
      border-radius: 100px;
      background-color: var(--system-negative-500);

      opacity: 0;

      #{$self}.is-checked & {
        opacity: 1;
      }
    }
  }

  &__icon {
    width: em(24, $fz);
    height: em(24, $fz);
  }

  &__popup {
    position: absolute;
    bottom: calc(100% + #{em(16, $fz)});
    right: 0;
    background-color: #fff;

    padding: em(16, $fz);
    min-width: em(380, $fz);
    height: em(761, $fz);
    background-color: var(--system-white);
    border-radius: em(16, $fz);
    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.12);

    overflow: hidden;

    display: none;
    flex-direction: column;

    @include screen(md) {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      height: 100%;
    }

    #{$self}.is-active & {
      display: flex;
    }
  }

  &__header {
    position: relative;
    display: flex;
    justify-content: flex-end;
  }

  &__title {
    $fz: 18;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-black);

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
  }

  &__actions {
    $fz: 18;
    font-size: em($fz, $fz_base);
    margin-top: auto;

    text-align: center;
  }

  &__clear {
    padding-top: em(16, $fz);
    padding-bottom: em(16, $fz);

    font-weight: 600;
    letter-spacing: 0.01em;

    color: var(--text-pallet-600);
  }

  &__button {
    width: 100%;
  }

  &__body {
    flex-grow: 1;
    overflow-y: scroll;

    scrollbar-width: none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }

  &__list {
    padding-top: em(10, $fz);
  }

  &__item {
    &:not(:first-child) {
      margin-top: em(20, $fz);
    }
  }
}

.chips {
  $self: &;

  display: flex;
  align-items: center;

  flex-wrap: wrap;
  gap: em(10, $fz);

  &--hashtag {
    #{$self}__label {
      $fz: 14;
      font-size: em($fz, $fz_base);
    }

    #{$self}__item {
      padding: em(4 6, $fz);
    }
  }

  &--tabs {
    @include screen(md) {
      flex-wrap: nowrap;
      overflow-y: scroll;

      scrollbar-width: none;
      -ms-overflow-style: none;

      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
  }

  &__item {
    display: flex;
    align-items: center;
    flex-shrink: 0;

    border-radius: em(8, $fz);
    padding: em(8 12, $fz);

    background-color: var(--system-white);

    cursor: pointer;

    transition: background-color $ease-main;

    &.is-checked {
      background-color: var(--system-primary);
    }
  }

  &__label {
    font-weight: 500;
    letter-spacing: 0.01em;
    color: var(--icon-gray);
  }

  &__close {
    width: em(16, $fz);
    height: em(16, $fz);
    margin-left: em(5, $fz);
  }
}
