.product-info {
  $self: &;

  display: flex;

  @include screen(md) {
    flex-direction: column;
  }

  &__preview {
    flex-shrink: 0;
  }

  &__detalis {
    @include screen(md, min) {
      padding-left: em(36, $fz);
    }

    @include screen(md) {
      margin-top: em(20, $fz);
    }
  }

  &__title {
    display: flex;
    align-items: center;

    #{$self}__img {
      width: em(58, $fz);
      height: em(58, $fz);
      margin-right: em(16, $fz);
    }

    #{$self}__label {
      $fz: 45;
      font-size: em($fz, $fz_base);
      font-weight: 600;
      color: var(--text-black);
    }
  }

  &__tooltip {
    margin-left: auto;
  }

  &__subtitle {
    $fz: 30;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    color: var(--text-pallet-900);

    margin-top: em(24, $fz);
  }

  &__tabs {
    margin-top: em(24, $fz);
    max-width: em(600, $fz);

    @include screen(md) {
      max-width: 100%;
    }
  }

  &__table {
    display: flex;
    justify-content: space-between;

    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-pallet-600);

    span {
      width: 50%;

      border-bottom: 1px solid var(--system-stroke);
      padding-top: em(20, $fz);
      padding-bottom: em(8, $fz);

      &:last-child {
        color: var(--text-pallet-900);
      }
    }
  }

  &__row {
    span:last-child {
      text-align: right;
    }
  }

  &__description {
    margin-top: em(24, $fz);

    #{$self}__subtitle {
      $fz: 18;
      font-size: em($fz, $fz_base);
      font-weight: 600;
      letter-spacing: 0.01em;
      color: var(--text-black);
    }

    #{$self}__text {
      letter-spacing: 0.01em;
      line-height: em(24, $fz);
      color: var(--text-pallet-600);

      &--top {
        @include ellipsis()
      }
    }

    .content {
      display: flex;
      flex-direction: column;

      margin-top: em(16, $fz);

      &__text {
        span {
          &.content__extra--visible {
            display: inline;
          }
        }
      }

      &__extra {
        display: none;

        &--visible {
          display: block;
        }
      }

      &__toggle {
        cursor: pointer;
        margin-left: auto;
        font-weight: 500;

        color: var(--text-primary-txt);

        &:hover {
          background-color: #f0f0f0;
        }
      }
    }
  }
}

.product-card-tags {
  display: flex;
  flex-wrap: wrap;
  gap: em(5, $fz);

  &__item {
    $fz: 14;
    font-size: em($fz, $fz_base);
    font-weight: 500;
    letter-spacing: 0.01em;
    color: var(--text-gray);
    border-radius: em(8, $fz);
    padding: em(8 12, $fz);
    background-color: var(--system-bg);
  }
}

.product-card {
  $self: &;

  position: relative;
  background-color: var(--system-white);

  display: flex;
  flex-direction: column;

  border-radius: em(12, $fz);
  width: 100%;

  border: 1px solid transparent;

  overflow: hidden;

  &--is-favorite {
    .button-tag__icon {
      color: var(--system-primary);
    }
  }

  &--is-search {
    flex-direction: row;

    #{$self}__fevarite {
      display: none;
    }

    #{$self}__preview {
      @include ratio(278, 165);
      flex-shrink: 0;
      flex-grow: 1;
    }

    #{$self}__info {
      max-width: em(354, $fz);
    }

    #{$self}__bage {
      width: em(40, $fz);
      height: em(40, $fz);

      img {
        width: em(24, $fz);
        height: em(24, $fz);
      }
    }
  }

  &__preview {
    position: relative;
    overflow: hidden;
    @include ratio(416, 312);
  }

  &__image {
    @include fill-img;

    transition: transform $ease-main;
  }

  &__bage,
  &__fevarite {
    position: absolute;
    top: em(16, $fz);
  }

  &__bage {
    width: auto;
    height: auto;
    left: em(16, $fz);
    width: em(64, $fz);
    height: em(64, $fz);

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: em(8, $fz);
    padding: em(8, $fz);

    background-color: rgba(#f5f5f5, 0.4);

    img {
      width: em(48, $fz);
      height: em(48, $fz);
    }
  }

  &__fevarite {
    color: transparent;
    right: em(16, $fz);
  }

  &__info {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: em(16, $fz);
  }

  &__title {
    $fz: 18;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-color-black);
  }

  &__tags {
    margin-top: auto;
  }
}
