$parent: '.navigation';

.navigation-list {
  $self: &;

  display: flex;

  #{$parent}--mobi & {
    flex-direction: column;
    padding-left: em(16, $fz);
    padding-right: em(16, $fz);
  }

  &__item {
    position: relative;

    text-transform: uppercase;
    letter-spacing: 0.02em;
    font-weight: 500;

    @include screen(md) {
      font-weight: 600;
      color: var(--text-pallet-900);
    }

    &::before {
      content: '';
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 0;
      height: 2px;

      opacity: 0;

      transition: opacity $ease-main, width $ease-main;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: em(16, $fz);
      transform: translateY(-50%) translateZ(0);

      width: em(16, $fz);
      height: em(16, $fz);
      border-radius: 100px;
      background-color: var(--system-primary);

      opacity: 0;
    }

    #{$parent}--mobi & {
      padding-top: em(16, $fz);
      padding-bottom: em(16, $fz);

      &::before {
        width: 100%;
        background: var(--system-stroke);
        opacity: 1;
      }
    }

    &:not(:first-child) {
      margin-left: em(24, $fz);

      #{$parent}--mobi & {
        margin-top: em(24, $fz);
        margin-left: 0;
      }
    }

    &.is-active {
      @include screen(md) {
        color: #000;
      }

      &::after {
        @include screen(md) {
          #{$parent}--mobi & {
            opacity: 1;
          }
        }
      }

      &::before {
        opacity: 1;
        width: 100%;
        background-color: var(--system-primary);
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__icon {
    width: em(24, $fz);
    height: em(24, $fz);
  }

  &__label {
    $fz: 14;
    font-size: em($fz, $fz_base);
  }
}

.navigation {
  $self: &;

  &--mobi {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;

    background-color: var(--system-white);

    display: none;

    &.is-active {
      display: block;
    }
  }


  &__header {
    position: relative;
    padding-left: em(28, $fz);
    padding-right: em(28, $fz);
    padding-top: em(32, $fz);
    padding-bottom: em(24, $fz);
  }

  &__close {
    margin-left: auto;
  }

  &__title {
    $fz: 18;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-black);

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
  }
}
