@import 'swiper/swiper-bundle.css';

.swiper-pagination-bullet {
  --swiper-pagination-bullet-inactive-color: var(--system-neutral-300);
  --swiper-pagination-color: var(--system-primary);
  opacity: 1;
}

.swiper-slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateZ(0);

  z-index: 20;

  border-radius: 100px;
  width: em(40, $fz);
  height: em( 40, $fz);
  background-color: var(--system-white);

  cursor: pointer;

  @include screen(md) {
    display: none;
  }

  &.swiper-button-disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    width: 80%;
    height: 80%;
  }

  &__prev {
    left: em(-80, $fz);
  }

  &__next {
    right: em(-80, $fz);
  }
}

.popular-products-slider {
  width: 100%;

  &__title {
    $fz: 30;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    color: var(--text-black);
  }

  &__swiper {
    width: 100%;
    padding-top: em(38, $fz);
    overflow: hidden;
  }

  &__wrapper {
    position: relative;
  }

  &__slide {}
}

.products-history-slider {
  position: relative;
  width: 100%;

  &__swiper {
    width: 100%;
    overflow: hidden;

    @include screen(md) {
      background-color: var(--system-white);
      border-radius: em(16, $fz);
      padding: em(8, $fz);
    }
  }

  &__slide {
    position: relative;
    max-width: em(1280, $fz);
  }

  &__preview {
    position: relative;
    overflow: hidden;
    border-radius: em(16, $fz);

    @include ratio(1280,525);

    @include screen(md) {
      @include ratio(327,201);
    }
  }

  &__image {
    @include fill-img;
  }

  &__description {
    position: absolute;
    right: em(20, $fz);
    bottom: em(20, $fz);
    z-index: 2;


    border-radius: em(16, $fz);

    display: flex;
    flex-direction: column;

    font-weight: 500;
    letter-spacing: 0.01em;
    color: var(--text-pallet-900);

    @include screen(md, min) {
      width: em(404, $fz);
      height: em(104, $fz);
      background-color: var(--system-bg);
      padding: em(18 14, $fz);
    }

    @include screen(md) {
      position: static;
      padding: em(16 8, $fz);
    }
  }

  &__fraction {
    $fz: 18;
    font-size: em($fz, $fz_base);
    font-weight: 600;

    margin-bottom: em(10, $fz);

    @include screen(md) {
      display: none;
    }

    .swiper-pagination-current {
      color: var(--text-primary-txt);
    }
  }

  &__pagination {
    position: absolute;
    top: calc(100% + #{em(12, $fz)}) !important;
    bottom: auto !important;
    text-align: center;
  }
}
.product-slider-thumbnails {
  &__slide {}

  &__thumbnail {
    position: relative;
    overflow: hidden;
    width: em(80, $fz);
    height: 100%;
    border-radius: em(12, $fz);

    cursor: pointer;
  }

  &__img {
    @include fill-img;
  }
}

.product-slider-preview {
  position: relative;
  overflow: hidden;

  &__slide {
    @include screen(md, min) {
      max-width: em(730, $fz);
    }

    @include screen(md) {
      max-height: em(280, $fz);
    }
  }

  &__item {
    position: relative;
    overflow: hidden;

    display: block;

    border-radius: em(16, $fz);
    @include ratio(640, 520);
  }

  &__img {
    @include fill-img;
  }

  &__pagination {
    position: absolute;
    bottom: em(6, $fz) !important;
    text-align: center;
    z-index: 10;
  }
}

.product-slider {
  display: flex;
  max-height: em(520, $fz);

  &__thumbnails {
    width: em(84, $fz);

    @include screen(md) {
      display: none;
    }
  }

  &__preview {
    width: em(640, $fz);
    overflow: hidden;
    border-radius: em(16, $fz);

    @include screen(md) {
      width: 100%;
    }
  }
}
