.overlay-tooltips {
  @extend .overlay;

  @include screen(md, min) {
    background-color: transparent !important;
  }
}

.tooltip {
  $self: &;

  position: relative;

  display: flex;
  justify-content: center;

  @include screen(md,min) {
    z-index: 1000;
  }

  &__popup {
    position: absolute;

    display: none;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    border-radius: em(16, $fz);

    background-color: var(--system-white);
    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.12);

    #{$self}--default & {
      top: 0;
      left: 50%;

      transform: translateX(-50%) translateY(#{calc(-100% - #{em(20, $fz)})}) translateZ(0);

      width: em(254, $fz);
      min-height: em(212, $fz);
      padding: em(24px 12px 16px 12px, $fz);
    }

    #{$self}--left & {
      top: 0;
      right: 100%;

      transform: translateX(#{em(-20, $fz)}) translateY(-45%) translateZ(0);

      width: em(317, $fz);
      min-height: em(148, $fz);
      padding: 16px 12px;
    }

    @include screen(md) {
      position: fixed;
      top: 50% !important;
      left: 50% !important;

      transform: translate3d(-50%, -50%, 0) !important;

      z-index: 1001;
    }

    #{$self}.is-active & {
      display: flex;
    }

    &::before {
      content: '';
      position: absolute;

      width: em(16, $fz);
      height: em(16, $fz);
      background-color: var(--system-white);

      transform-origin: 14px 14px;
      transform: rotate(45deg) translate3d(-50%, 50%, 0);


      #{$self}--default & {
        left: 50%;
        bottom: em(-10, $fz);
      }

      #{$self}--left & {
        top: 50%;
        right: em(-10, $fz);
      }

      @include screen(md) {
        display: none;
      }
    }
  }

  &__close {
    position: absolute;
    top: em(8, $fz);
    right: em(8, $fz);

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: var(--system-bg);

    border-radius: em(29, $fz);
    width: em(32, $fz);
    height: em(32, $fz);

    @include screen(md, min) {
      display: none;
    }

    #{$self}__icon {
      width: em(24, $fz);
      height: em(24, $fz);
    }
  }

  &__preview {
    display: flex;
    align-items: center;
    justify-content: center;

    width: em(92, $fz);
    height: em(92, $fz);
    background-color: var(--system-primary-100);
    border-radius: 100px;

    margin-bottom: em(12, $fz);
  }

  &__icon {
    width: em(64, $fz);
    height: em(64, $fz);
  }

  &__button {
    position: relative;
    overflow: hidden;
    width: em(32, $fz);
    height: em(32, $fz);

    color: var(--system-neutral-400);

    #{$self}__icon {
      @include fill-img;
    }

    #{$self}.is-active & {
      @include screen(md, min) {
        color: var(--system-primary);
      }
    }
  }

  &__title {
    $fz: 14;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-black);

    text-align: center;
  }

  &__msg {
    $fz: 14;
    font-size: em($fz, $fz_base);
    letter-spacing: 0.01em;

    color: var(--text-pallet-600);

    margin-top: em(5, $fz);

    #{$self}--default & {
      text-align: center;
    }
  }
}
