.products-count {
  $self: &;

  position: relative;
  display: flex;
  align-items: center;

  cursor: pointer;

  &.is-active #{$self}__options {
    display: block;
  }

  &__selected {
    display: flex;
    align-items: center;
    border: 1px solid #efefef;
    border-radius: em(12, $fz);

    margin-right: em(10, $fz);
    padding: em(12, $fz);

    padding: em(4, $fz);

    background-color: var(--system-white);

    #{$self}__label {
      display: flex;
      align-items: center;
      justify-content: center;

      border-radius: em(12, $fz);
      padding: em(8 12, $fz);
      background-color: var(--system-bg);
    }
  }

  &__icon {
    width: em(16, $fz);
    height: em(16, $fz);
    margin-left: em(5, $fz);
  }

  &__label {
    $fz: 13;
    font-size: em($fz, $fz_base);
    font-weight: 500;
    letter-spacing: 0.01em;
    color: var(--text-pallet-600);
  }

  &__options {
    display: none;
    position: absolute;
    top: calc(100% + #{em(5, $fz)});
    left: 0;
    margin: 0;
    padding: em(4, $fz);
    width: 100%;
    max-width: em(60, $fz);
    background-color: var(--system-white);
    border-radius: em(16, $fz);
    text-align: center;
    z-index: 1000;

    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.12);
  }

  &__option {
    padding: em(10 20, $fz);
    cursor: pointer;
    border-radius: em(12, $fz);
    padding: em(6 8, $fz);

    &:hover {
      background-color: var(--system-bg);
    }
  }
}

.page-count {
  display: flex;
  align-items: center;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: em(8, $fz);
    padding: em(12 8, $fz);
    min-width: em(40, $fz);
    min-height: em(44, $fz);

    transition: background-color $ease-main;

    &.is-active,
    &:hover {
      background-color: var(--system-primary);
    }
  }

  &__icon {
    width: em(24, $fz);
    height: em(24, $fz);
  }
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__count {
    @include screen(md) {
      display: none;
    }
  }
}
