.lang {
  $self: &;

  position: relative;

  &.is-active #{$self}__options {
    display: block;
  }

  &__selected {
    display: flex;
    align-items: center;
    border: 1px solid var(--system-primary);
    border-radius: em(8, $fz);
    padding: em(12, $fz);

    cursor: pointer;
  }

  &__icon {
    width: em(16, $fz);
    height: em(16, $fz);
    margin-left: em(5, $fz);
  }

  &__label {
    $fz: 14;
    font-size: em($fz, $fz_base);
  }

  &__options {
    display: none;
    position: absolute;
    top: calc(100% + #{em(5, $fz)});
    left: 0;
    margin: 0;
    padding: em(4, $fz);
    width: 100%;
    background-color: var(--system-white);
    border-radius: em(16, $fz);
    z-index: 1000;

    box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.12);
  }

  &__option {
    padding: em(10 20, $fz);
    cursor: pointer;
    border-radius: em(12, $fz);
    padding: em(6 8, $fz);

    &:hover {
      background-color: var(--system-bg);
    }
  }
}
