.footer {
  &:not(.footer__social) {
    color: var(--text-pallet-600);
  }

  &__container {
    padding: em(88 88 33, $fz);

    border-radius: em(16 16 0 0, $fz);
    background-color: var(--system-white);

    @include screen(md) {
      padding: em(80 16, $fz);
    }
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;

    position: relative;

    &:not(:first-child) {
      margin-top: em(88, $fz);
    }

    @include screen(md) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  &__col {
    display: flex;
    align-items: center;

    &--center {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
    }
  }

  &__label {
    $fz: 13;
    font-size: em($fz, $fz_base);
  }

  &__social {
    margin-right: em(122, $fz);
  }
}
