.section {
  $self: &;

  &--lg {
    margin-top: em(80, $fz);
  }

  &--sm {
    margin-top: em(40, $fz);

    #{$self}__header {
      margin-bottom: em(28, $fz);
    }
  }

  &--xs {
    margin-top: em(16, $fz);
  }

  &__header {
    display: flex;
    justify-content: space-between;

    margin-bottom: em(40, $fz);

    &--is-centred {
      justify-content: center;
    }

    @include screen(md) {
      flex-direction: column;
    }
  }

  &__title {
    $fz: 55;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    color: var(--text-black);
  }

  &__chips {
    max-width: em(632, $fz);

    @include screen(md) {
      margin-top: em(24, $fz);
    }
  }

  &__actions {
    display: flex;

    @include screen(md) {
      margin-top: em(24, $fz);
    }
  }

  &__action {
    color: transparent;

    @include screen(md) {
      min-width: auto;
      width: em(40, $fz);
      height: em(40, $fz);

      &:first-child {
        margin-right: auto;
      }

      span {
        display: none;
      }

      svg {
        margin-left: 0;
      }
    }

    &:not(:first-child) {
      margin-left: em(10, $fz);
    }
  }
}
