.merch-banner {
  $self: &;

  display: flex;
  position: relative;

  border-radius: em(24, $fz);
  background-color: var(--system-white);

  overflow: hidden;

  @include screen(md, min) {
    max-height: em(436, $fz);
  }

  @include screen(md) {
    flex-direction: column;
  }

  &__preview {
    position: relative;
    overflow: hidden;

    width: em(664, $fz);
    min-height: em(436, $fz);
    flex-shrink: 0;
  }

  &__image {
    @include fill-img;
  }

  &__info  {
    display: flex;
    flex-direction: column;

    padding: em(34 24 21 47, $fz);
  }

  &__logo {
    @include screen(md, min) {
      margin-left: auto;
    }
  }

  &__title {
    $fz: 38;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;

    color: var(--black-new);

    margin-top: em(67, $fz);
  }

  &__description {
    $fz: 17;
    font-size: em($fz, $fz_base);
    line-height: em(24, $fz);
    font-weight: 500;
    color: #707685;

    margin-top: em(17, $fz);
  }

  &__btn {
    $fz: 14;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;

    display: flex;
    align-items: center;
    justify-content: center;

    width: em(100,$fz);
    height: em(100,$fz);

    margin-top: auto;
    margin-left: auto;

    background-color: #fcea2b;
    color: var(--text-color-black);
    border-radius: 100px;
    cursor: pointer;
  }
}

.repair-banner {
  position: relative;
  border-radius: em(16, $fz);

  @include screen(md, min) {
    background-color: var(--system-white);
    padding: em(16, $fz);
  }

  &__slider {
    max-height: em(375, $fz);
    border-radius: em(16, $fz);
    overflow: hidden;
  }

  &__preview {
    position: relative;
    overflow: hidden;
    border-radius: em(16, $fz);

    @include ratio(616, 375);
  }

  &__img {
    @include fill-img
  }

  &__pagination {
    position: absolute;
    top: calc(100% + #{em(12, $fz)}) !important;
    bottom: auto !important;
    text-align: center;
  }
}
