.radio-inputs-group {
  display: flex;
  flex-direction: column;
  gap: em(12, $fz);

  &__title {
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-pallet-900);
  }

  input {
    display: none;

    &:checked + span {
      color: var(--system-black);

      &::after, &::before {
        opacity: 1;
        color: var(--system-primary);
      }
    }
  }

  &__radio {
    cursor: pointer;
  }

  &__label {
    position: relative;
    font-weight: 500;
    color: var(--text-pallet-400);

    padding-left: em(30, $fz);

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;

      transform: translateY(-50%);

      display: block;
      width: em(20, $fz);
      height: em(20, $fz);

      border-radius: 100px;
      border: 1px solid currentColor;
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: em(11, $fz);

      transform: translate(-50%, -50%);

      display: block;
      width: em(8, $fz);
      height: em(8, $fz);

      border-radius: 100px;
      background: currentColor;
      opacity: 0;
    }
  }
}

.input-field {
  $self: &;

  position: relative;
  width: 100%;

  .iti {
    width: 100%;

    & + .input-field__label {
      left: em(50, $fz);
    }
  }

  &__label {
    position: absolute;
    top: 50%;
    left: em(12, $fz);
    color: var(--text-pallet-400);

    transform: translateY(-50%);

    &::after {
      content: '*';
      color: var(--system-negative-500);
      margin-left: em(3, $fz);
      display: none;

      #{$self}--is-required & {
        display: inline-block;
      }
    }

    #{$self}--is-value &,
    #{$self}--is-focus & {
      display: none;
    }
  }

  &__error {
    $fz: 13;
    font-size: em($fz, $fz_base);
    position: absolute;
    top: calc(100% + 1px);
    left: 0;
    color: var(--system-negative-500);

    display: none;

    #{$self}--is-error & {
      display: inline-block;
    }
  }

  &__input {
    border-radius: em(12, $fz);
    padding: em(16 12, $fz);

    border: 1px solid transparent;
    background: var(--system-neutral-50);
    font-weight: 500;
    letter-spacing: 0.01em;
    color: var(--text-pallet-400);
    width: 100%;

    &[type="textarea"] {
      min-height: em(112, $fz);

     + #{$self}__label {
        top: em(12, $fz);
       transform: translateY(0);
      }
    }


    #{$self}--is-value & {
      border-color: var(--system-primary);
      background: var(--system-white);
      color: var(--text-black);
    }

    #{$self}--is-focus & {
      border-color: var(--system-stroke);
      background: var(--system-white);
      color: var(--text-black);
    }

    #{$self}--is-error:not(#{$self}--is-focus) & {
      border-color: var(--system-negative-500);
    }
  }
}

.image-upload-field {
  $self: &;

  display: flex;
  flex-direction: column;

  position: relative;


  &__tooltip {
    margin-top: em(24, $fz);
  }

  &__label {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: relative;

    color: var(--system-neutral-400);

    border: 1px dashed;
    border-radius: em(16, $fz);
    overflow: hidden;

    @include screen(md, 'min') {
      width: em(117, $fz);
      height: em(117, $fz);
    }

    cursor: pointer;

    @include screen(md) {
      width: 100%;
      height: em(147, $fz);
    }

    &:hover {
      color: var(--system-primary);
    }
  }

  &__icon {
    width: em(24, $fz);
    height: em(24, $fz);
  }

  &__descr {
    $fz: 13;
    font-size: em($fz, $fz_base);
    font-weight: 500;
    letter-spacing: 0.01em;
    text-align: center;

    max-width: 90%;
  }

  &__input {
    display: none;
  }

  &__image {
    @include fill-img;
    border-radius: em(16, $fz);
    z-index: 5;
    display: none;

    #{$self}.is-loaded & {
      display: block;
    }
  }

  &__close {
    position: absolute;
    top: em(8, $fz);
    right: em(8, $fz);

    width: em(24, $fz);
    height: em(24, $fz);
    background: rgba(255, 255, 255, 0.8);
    border-radius: 100px;
    z-index: 6;

    display: flex;
    align-items: center;
    justify-content: center;

    display: none;

    #{$self}.is-loaded & {
      display: flex;
    }

    svg {
      width: em(12, $fz);
      height: em(12, $fz);
    }
  }
}

.image-upload-list {
  display: inline-flex;

  flex-wrap: wrap;
  width: 100%;

  &__item {
    display: flex;
    align-items: center;

    @include screen(md) {
      width: calc(50% - #{em(16, $fz)});
    }
  }
}

.image-upload {
  $self: &;

  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: em(24, $fz);

  &__list {
    gap: em(15, $fz);
  }

  &__button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;

    font-weight: 600;
    letter-spacing: 0.01em;
    text-align: center;
    color: var(--text-primary-txt);

    margin: auto 0;

    @include screen(md) {
      position: absolute;
      right: em(16, $fz);
      bottom: em(16, $fz);

      border-radius: em(8, $fz);
      padding: em(8 12, $fz);

      background-color: var(--system-primary);

      #{$self}__label {
        $fz: 14;
        font-size: em($fz, $fz_base);
        font-weight: 500;
        letter-spacing: 0.01em;
        color: var(--text-pallet-900);
      }

      #{$self}__icon {
        display: none;
      }
    }
  }

  &__icon {
    width: em(25, $fz);
    height: em(25, $fz);
  }

  &__label {
    $fz: 18;
    font-size: em($fz, $fz_base);
  }
}

.form-section {
  $self: &;

  position: relative;
  padding: em(30 38, $fz);
  border-radius: em(24, $fz);

  background-color: var(--system-white);

  &--decor {
    position: relative;

    #{$self}__description {
      max-width: em(658, $fz);
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__decor {
    position: absolute;
    top: 50%;

    width: em(71, $fz);
    height: em(136, $fz);
    transform: translateY(-50%) translateZ(0);

    &--before {
      left: em(38, $fz);
    }

    &--after {
      right: em(38, $fz);
    }

    @include screen(md) {
      display: none;
    }
  }

  &__title {
    $fz: 24;
    font-size: em($fz, $fz_base);
    text-align: center;
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-pallet-900);

    margin-bottom: em(24, $fz);
  }

  &__description {
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-pallet-900);

    @include screen(md, min) {
      text-align: center;
    }

    #{$self}__label {
      $fz: 24;
      font-size: em($fz, $fz_base);
    }
  }

  &__content {
    &--fields {
      display: flex;
      flex-wrap: wrap;
      gap: em(18, $fz);

      @include screen(md, min) {
        .input-field:not(input[type="textarea"]) {
          flex-grow: 1;
          width: calc(50% - #{em(15, $fz)});
        }
      }
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: em(24, $fz);

    @include screen(md) {
      flex-direction: column-reverse;
    }
  }

  &__action {
    display: flex;
    align-items: center;

    border-radius: em(24, $fz);
    font-weight: 600;
    padding: em(16 24, $fz);

    background: var(--system-neutral-50);

    span {
      $fz: 18;
      font-size: em($fz, $fz_base);
    }

    @include screen(md) {
      width: 100%;
      justify-content: center;
    }

    &--button {
      svg {
        width: em(16, $fz);
        height: em(16, $fz);

        margin-left: em(5, $fz);
      }
    }

    &--label {
      color: var(--text-pallet-400);

      @include screen(md) {
        background-color: transparent;
      }

      svg {
        width: em(20, $fz);
        height: em(20, $fz);

        margin-right: em(5, $fz);
      }

      span {
        span {
          margin-left: em(5, $fz);
          color: var(--text-primary-txt);
        }
      }
    }
  }
}

.form-checkbox {
  $self: &;

  display: flex;
  align-items: center;

  cursor: pointer;

  &__input {
    display: none;

    &:checked + #{$self}__check #{$self}__icon {
      opacity: 1;
    }
  }

  &__check {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    width: em(24, $fz);
    height: em(24, $fz);
    border-radius: 6px;
    border: 1px solid var(--text-primary-txt);
    background: var(--background-variant-1);
    color: var(--text-primary-txt);
  }

  &__icon {
    width: em(14, $fz);
    height: em(14, $fz);

    opacity: 0;
  }

  &__label {
    $fz: 14;
    font-size: em($fz, $fz_base);
    font-weight: 600;
    letter-spacing: 0.01em;
    color: var(--text-pallet-600);
    max-width: em(422, $fz);
    margin-left: em(8, $fz);
  }
}

.form {
  $self: &;

  &__section {
    width: 100%;
    max-width: em(848, $fz);
    margin-left: auto;
    margin-right: auto;

    &:not(:first-child) {
      margin-top: em(20, $fz);
    }
  }

  &__radio-fields {
    display: flex;
    flex-wrap: wrap;
    gap: em(15, $fz);

    margin-top: em(24, $fz);
  }

  &__radio {
    @include screen(md, min) {
      &:nth-child(odd) {
        width: 60%;
      }
    }
  }

  &__certificate {
    position: relative;
    overflow: hidden;

    @include ratio(777, 549);
    width: 100%;
  }

  &__image {
    @include fill-img;
  }
}
