.button-up {
  display: flex;
  align-items: center;

  &__icon {
    width: em(24, $fz);
    height: em(24, $fz);

    margin-right: em(5, $fz);
  }

  &__label {
    font-weight: 500;
    letter-spacing: 0.01em;
    color: #535d6b;
  }
}

.layout {
  display: flex;
  flex-direction: column;

  min-height: 100vh;

  &__sidebar {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    padding: em(115 48, $fz);
    max-width: em(185, $fz);
    width: 100%;
    background: #ececec;
    z-index: 1002;

    cursor: pointer;

    display: none;

    &.is-active {
      display: block;
    }

    @include screen(lg) {
      font-size: 0.7vw;
    }
  }

  &__content {
    margin-bottom: em(120, $fz);
  }

  &__pagination {
    padding-top: em(120, $fz);

    @include screen(md) {
      padding-top: em(100, $fz);
      justify-content: center;
    }
  }

  &__footer {
    margin-top: auto;
  }
}

.is-desktop {
  @include screen(md) {
    display: none !important;
  }
}

.is-mobi {
  @include screen(md, min) {
    display: none !important;
  }
}
