.scroll-top {
  border-radius: em(12, $fz);
  padding: em(16, $fz);
  width: em(56, $fz);
  height: em(56, $fz);

  box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.12);
  background: var(--system-white);

  display: none;
  align-items: center;
  justify-content: center;

  &.is-active {
    display: flex;
  }

  &__icon {
    width: em(24, $fz);
    height: em(24, $fz);
  }
}

.action-bar {
  position: fixed;
  bottom: em(12, $fz);
  right: calc((100vw - #{em($container-width, $fz)}) / 2 + #{em(16, $fz)});
  z-index: 999;

  display: flex;
  align-items: center;

  @include screen(md) {
    right: em(12, $fz);
  }

  &__item {
    &:not(:first-child) {
      margin-left: em(12, $fz);
    }
  }
}
