.header {
  padding-top: em(24, $fz);

  @include screen(md) {
    padding-left: em(16, $fz);
    padding-right: em(16, $fz);
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    position: relative;

    padding: em(40 28 34, $fz);

    border-radius: em(16, $fz);
    background-color: var(--system-white);

    @include screen(md) {
      padding: em(16 12 70, $fz);
    }
  }

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);

    @include screen(md) {
      top: calc(100% - #{em(33, $fz)});
    }
  }

  &__actions {
    display: flex;
  }

  &__social {
    margin-right: em(86, $fz);
  }

  &__action {
    margin-left: em(12, $fz);
  }
}
