.social-item {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: var(--text-pallet-900);
  letter-spacing: 0.01em;
  font-weight: 500;
  line-height: 1.4;

  transition: color $ease-main;

  &:hover {
    color: var(--system-primary);
  }

  &__icon {
    width: em(20, $fz);
    height: em(20, $fz);

    fill: transparent;
  }

  &__label {
    $fz: 13;
    font-size: em($fz, $fz_base);
  }
}
