.catalog {
  &__grid {
    display: flex;
    flex-wrap: wrap;
    gap: em(16, $fz);
  }

  &__item {
    max-width: em(416, $fz);
    min-height: em(436, $fz);

    @include screen(md) {
      width: calc(50% - #{em(8, $fz)});
      max-width: 100% !important;
    }

    &--search {
      max-width: calc(50% - #{em(8, $fz)});
      max-height: em(165, $fz);
      min-height: auto;
    }
  }
}
